.card {
    border: none;
    border-radius: .625rem;

    .card-title {
        height: 85px;
    }

    picture {
        border-radius: inherit;
    }

    .card-body {
        position: relative;
        min-height: 220px;

        .card-bar {
            .card-text {
                height: 130px;
                overflow-y: auto;
            }
        }
        .pos-icon {
            position: absolute;
            bottom: 10px;
            right: 10px;
        }
        .min-height-duyuru {
            height: 100px !important;
            overflow-y: auto;
        }
    }

    .custom-card {
        height: 288px;
    }
    .card-content-border {
        position: absolute;
        border-left: 2px solid #FFF;
        padding-left: 20px;
        padding-right: 20px;
    }

    .card-img-top {
        border-top-left-radius: inherit;
        border-top-right-radius: inherit;
        width: 100%;
        object-fit: cover;
        margin-bottom: -3rem;
    }

    &.custom-card {
        .card-body {
            min-height: auto;
        }
    }

}